<template>
  <div>
    <Modal
      v-model="modelAdd"
      width="700"
      :mask-closable="false"
      :closable="false"
      :transfer="false"
    >
      <p slot="header">
        <span style="font-size:20px">话术详情</span>
      </p>

      <div style="margin-bottom:10px">
        <Button style="displa:inline-block" type="primary" @click="addText">新增文本</Button>
        <Button v-if="isShowTask" style="displa:inline-block" type="primary" @click="addTask">新增任务</Button>
        <Button v-if="!isShowTask" style="displa:inline-block" disabled type="primary">新增任务</Button>
      </div>

      <div>
        <div
          style="background-color:rgba(235, 240, 251, 1);width:638px;height:120px;margin-bottom:10px"
          v-for="(item,index) in addNode.nodeTextList"
          :key="index"
        >
          <span
            style="color:#07175D;display:inline-block;text-align:left;margin-left:10px;margin-top:10px"
          >话术文本</span>
          <a
            style="color:red;display:inline-block;float:right;margin-right:10px;margin-top:10px"
            @click="deleteText(index)"
          >删除</a>
          <Input
            type="textarea"
            :rows="3"
            placeholder="输入话术文本"
            v-model="item.content"
            style="width:620px;height:78px;margin-left:10px;margin-top:5px"
          ></Input>
        </div>
      </div>
      <br />
      <div v-if="isShowTaskAdd">
        <div
          style="background-color:rgba(235, 240, 251, 1);width:638px;height:168px;margin-bottom:10px"
        >
          <span
            style="color:#07175D;display:inline-block;text-align:left;margin-left:10px;margin-top:10px"
          >任务资源</span>
          <a
            style="color:red;display:inline-block;float:right;margin-right:10px;margin-top:10px"
            @click="deleteTask"
          >删除</a>
          <div style="margin-top:10px">
            <span
              style="color:#07175D;display:inline-block;text-align:left;margin-left:10px;margin-top:10px"
            >请选择任务资源：</span>
            <Select
              v-model="addNode.taskResourceMap.type"
              @on-change="select1"
              style="width: 70px"
            >
              <Option
                v-for="(element,index2) in resourceType"
                :value="element.type"
                :key="index2"
              >{{element.value}}</Option>
            </Select>
            <Select
              v-model="addNode.taskResourceMap.contentId"
              style="width: 400px"
            >
              <Option
                v-for="(element1,index3) in contentList"
                :key="index3"
                :value="element1.contentId"
              >{{element1.title}}</Option>
            </Select>
          </div>
          <Checkbox
            v-model="addNode.taskResourceMap.isComment"
            style="margin-left:10px;margin-top:10px"
          >接受用户评价</Checkbox>
        </div>
      </div>
      <br />
      <div>
        <div style="width:57px;height:20px">
          <p>
            <span style="color: #07175D;font-size: 14px;">触发条件</span>
          </p>
        </div>
        <br />
        <div>
          <Select v-model="addNode.term" style="width:100px;display:inline-block">
            <Option value="0">无</Option>
            <Option value="1">用户选择</Option>
            <Option value="2">用户输入</Option>
          </Select>
          <Input
            v-if="addNode.term!=0"
            style="width:200px;display:inline-block;text-align:center"
            type="text"
            placeholder="请输入内容"
            v-model="addNode.termValue"
          ></Input>
        </div>
      </div>
      <div slot="footer">
        <Button @click="cancel">取消</Button>
        <Button type="primary" @click="save">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
export default {
  data() {
    return {
      modelAdd: true,
      isShowTask: true,
      isShowTaskAdd: false,
      contentList:[],
      resourceType: [
        {
          type: 1,
          value: "音频"
        },
        {
          type: 2,
          value: "视频"
        },
        {
          type: 3,
          value: "电子书"
        },
        {
          type: 4,
          value: "问卷"
        },
        {
          type: 5,
          value: "表单"
        },
        {
          type: 6,
          value: "小游戏"
        }
      ],
      addNode: {
        verbalTrickId: "",
        stageId: "",
        upId: "",
        term: "0",
        termValue: "",
        nodeTextList: [],
        taskResourceMap: {}
      },
      addNode1: {
        verbalTrickId: "",
        stageId: "",
        nodeTextList: [],
        taskResourceMap: {}
      }
    };
  },
  methods: {
    addText() {
      this.addNode.nodeTextList.push({ content: "" });
    },
    addTask() {
      this.addNode.taskResourceMap = {
        isComment: 2,
        type: 1,
        contentId: ""
      };
      this.$axios
        .post("api/taskResource/getTaskResourceList", {
          type: this.addNode.taskResourceMap.type
        })
        .then(res => {
          this.contentList = res.data.data;
          console.log('contentId',this.contentList)
          this.addNode.taskResourceMap.contentId = this.contentList[0].contentId;   
        });
      this.isShowTaskAdd = true;
      this.isShowTask = false;
    },
    select1(e) {
      this.addNode.taskResourceMap.type = e;
      this.$axios
        .post("api/taskResource/getTaskResourceList", { type: e })
        .then(res => {
          this.contentList = res.data.data;
          this.addNode.taskResourceMap.contentId = res.data.data[0].contentId;
        });
    },
    deleteText(index) {
      this.addNode.nodeTextList.splice(index, 1);
    },
    deleteTask() {
      this.addNode.taskResourceMap = {};
      this.isShowTaskAdd = false;
      this.isShowTask = true;
    },
    save() {
      console.log(this.addNode);
      this.addNode1.verbalTrickId = this.addNode.verbalTrickId;
      this.addNode1.stageId = this.addNode.stageId;
      if (this.addNode.upId) {
        this.addNode1.upId = this.addNode.upId;
      }
      if (this.addNode.term && this.addNode.termValue&&this.addNode.term!=0) {
        this.addNode1.term = parseInt(this.addNode.term);
        this.addNode1.termValue = this.addNode.termValue;
      }
      this.addNode1.nodeTextList = this.addNode.nodeTextList;
      if (JSON.stringify(this.addNode.taskResourceMap) != "{}") {
        if (this.addNode.taskResourceMap.isComment == true) {
          this.addNode.taskResourceMap.isComment = 1;
        }
        if (this.addNode.taskResourceMap.isComment == false) {
          this.addNode.taskResourceMap.isComment = 2;
        }
        this.addNode1.taskResourceMap.isComment = this.addNode.taskResourceMap.isComment;
        this.addNode1.taskResourceMap = this.addNode.taskResourceMap
      } else {
        delete this.addNode1.taskResourceMap;
      }
      if (this.addNode1.nodeTextList) {
        this.$axios
          .post("api/verbalTrickNode/addVerbalTrickNode", this.addNode1)
          .then(res => {
            this.$router.go(-1);
          });
      }
      this.isShowTaskAdd = false;
      this.addNode.nodeTextList = [];
      this.addNode.taskResourceMap = {};
    },
    cancel() {
      this.$router.go(-1);
    }
  },
  mounted() {
    this.addNode.verbalTrickId = this.$route.query.verbalTrickId;
    this.addNode.stageId = this.$route.query.momentChild;
    this.index1 = this.$route.query.index1;
    if (this.$route.query.upId) {
      this.addNode.upId = this.$route.query.upId;
    }
  }
};
</script>


